import { Component, OnInit } from '@angular/core';
import { ProductService } from '../../../../../auth/_services/product.service';
import { QuickSidebarComponent } from '../../../../layouts/quick-sidebar/quick-sidebar.component';
// import { SideBarService } from '../../../../layouts/side-bar/side-bar.service';
// import { SideBarToggleComponent } from '../../../../layouts/side-bar-toggle/side-bar-toggle.component';
import { DataService } from '../../../../../_services/data.service';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { Router, ActivatedRoute } from "@angular/router";
import { ToastrService } from 'ngx-toastr';
@Component({
    selector: 'app-tag-list',
    templateUrl: "tag-list.component.html",
    styles: []
})
export class TagListComponent implements OnInit {


    form: FormGroup;
    private formSumitAttempt: boolean;

    constructor(private toastr: ToastrService, private route: ActivatedRoute, private router: Router, private formBuilder: FormBuilder, private _dataService: DataService, private _productService: ProductService) { }
    public tagData;
    public searchValue = "";
    public dropDownValue = '';
    public noOfPage = '';
    p: number = 1;
    currentUser: any;
    public userData;
    public attributeData = '';
    public selectedStatus = '';
    public product_id: any;
    public user_id = '';
    ngOnInit() {


        this.form = this.formBuilder.group({
            tag_name: [null, Validators.required],
            description: [null, Validators.required],
            status: [null, Validators.required]
        });
        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));

        this.user_id = this.currentUser.success.id;

        this._productService.getTag().subscribe(
            data => {
                this.tagData = data.success;
                this.noOfPage = data.success.length;
                console.log('getTag', data.success);

            }
        );

    }


    isFieldValid(field: string) {
        return (
            //console.log('this.form.get',this.form.get(field));
            (!this.form.get(field).valid && this.form.get(field).touched) ||
            (this.form.get(field).untouched && this.formSumitAttempt)
        );
    }

    displayFieldCss(field: string) {
        return {
            'has-error': this.isFieldValid(field),
            'has-feedback': this.isFieldValid(field)
        };
    }

    onSubmit() {
        this.formSumitAttempt = true;
        if (this.form.valid) {
            let fk_product_id = 1;
            this._productService.addTag({ form_data: this.form.value, fk_user_id: this.user_id, fk_product_id: parseInt(this.product_id) }).subscribe(
                data => {
                    //this.attributeData = data.success;
                    console.log(data);
                    this.attributeData = data.data;
                    this.toastr.success('Product Tag Added Successfully', 'Success Message');
                    // that.form.c;
                    this.reset();

                    this._productService.getTag().subscribe(
                        data => {
                            //console.log(params.id);
                            this.tagData = data.success;
                            this.noOfPage = data.success.length;

                        }
                    );


                }
            );
        }
        /*this.route.params.subscribe(
            params => {
                if (params.id) {
                    this.product_id = params.id;

                    
                }
            }
        );*/

    }

    reset() {
        if (this.selectedStatus != '') {
            this.form.reset();
            this.selectedStatus = '';
            this.formSumitAttempt = false;
        }
    }

    public searchTag() {
        this._productService.tagSearching({ search_value: this.searchValue, dropdown_value: this.dropDownValue, user_id: this.currentUser.success.id }).subscribe(
            data => {

                console.log(this.currentUser.success.id);
                this.tagData = data.success;
            }
        );
    }
    public tag_delete(del_id) {

        console.log(del_id);

        let value = confirm('Are you want to delete this tag ?');

        if (value) {
            this._productService.deleteTag(del_id).subscribe(
                data => {
                    this.tagData = data.data;
                }
            );
        }

    }
    public changeStatus(user: any) {

        console.log(user.status);
        if (user.status == 'Active') {
            user.status = 'Inactive';

            this._productService.updateTagStatus(user.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        } else {
            user.status = 'Active';
            this._productService.updateTagStatus(user.id).subscribe(
                data => {
                    console.log('update', data);
                }
            );
        }
    }
}
